<template>
  <div id="app">
    <LayoutSidebar></LayoutSidebar>
    <LayoutMain>
      <LayoutToolbar slot="toolbar">
        <DiamondButton type="back" icon="arrow-left"></DiamondButton>
        <ButtonGroup>
          <DiamondButton type="light"></DiamondButton>
          <DiamondButton></DiamondButton>
        </ButtonGroup> 
      </LayoutToolbar>

      <Panel title="">
        <div slot="btns">
          <el-button icon="plus" :plain="true" type="primary"></el-button>
        </div>
        
        <SearchForm>
          <el-form label-position="top" :model="searchForm" ref="searchForm">
            <el-row :gutter="20">
              <el-col :span="6">
                <el-form-item label="" prop="name">
                  <el-input v-model="searchForm.name"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="2" prop="name2">
                  <el-input v-model="searchForm.name2"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="" prop="cascader">
                  <Cascader v-model="searchForm.cascader" @change="onChange" :options="options"></Cascader>
                  <!-- <el-input v-model="searchForm.region"></el-input> -->
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="" prop="type">
                  <el-input v-model="searchForm.type"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <div v-show="formDetailToggle">
              <el-row :gutter="20">
                <el-col :span="6">
                  <el-form-item label="" prop="region">
                    <el-input v-model="searchForm.region"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                  <el-form-item label="" prop="date1">
                    <el-date-picker
                      v-model="searchForm.date1"
                      type="daterange"
                      placeholder=""
                      style="width: 100%;">
                    </el-date-picker>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                  <el-form-item label="" prop="date2">
                    <el-date-picker
                      v-model="searchForm.date2"
                      type="month"
                      placeholder=""
                      style="width: 100%;">
                    </el-date-picker>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                  <el-form-item label="" prop="time">
                    <el-select v-model="searchForm.time" placeholder="" clearable>
                      <el-option label="" value="shanghai"></el-option>
                      <el-option label="" value="beijing"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-row>
            </div>
          </el-form>
          <div slot="btns">
            <SearchFormToggle :is-open="formDetailToggle" @toggle="onToggle"></SearchFormToggle>
            <el-button 
              class="search-form-btn"
              size="large" 
              type="primary" 
              :loading="loading" 
              @click.native="onSearch" > </el-button>
            <el-button size="large" class="search-form-btn" @click.native.prevent="onClear"> </el-button>
          </div>  
        </SearchForm>
        <PanelTable class="table-wrapper" v-loading="loading" element-loading-text="">
          <el-table
            :data="tableData"
            stripe
            border
            style="width: 100%">
            
            <el-table-column
              fixed
              prop="createdDate"
              label=""
              width="180">
            </el-table-column>
            <!-- <el-table-column
              inline-template
              label=""
              width="180">

              <TableCell>
                <TableCellItem></TableCellItem> 
                <TableCellItem>
                  <el-select placeholder="" clearable>
                    <el-option label="" value="shanghai"></el-option>
                    <el-option label="" value="beijing"></el-option>
                  </el-select>
                </TableCellItem> 
                <TableCellItem></TableCellItem> 
              </TableCell>

            </el-table-column> -->
            <!-- <el-table-column
              inline-template
              label=""
              width="180">
              <TableCell>
                <TableCellItem>
                  <el-select placeholder="" clearable>
                    <el-option label="" value="shanghai"></el-option>
                    <el-option label="" value="beijing"></el-option>
                  </el-select>
                </TableCellItem> 
                <TableCellItem></TableCellItem> 
                <TableCellItem></TableCellItem> 
              </TableCell>

            </el-table-column> -->

            <el-table-column
              inline-template
              label="">

              <TableTip>
                <div slot="tip">: </div>
                <span>{{ row.profile }}</span>
              </TableTip>
            </el-table-column>
            
            <el-table-column
              prop="name"
              label=""
              width="180">
            </el-table-column>
            <el-table-column
              prop="email"
              label=""
              width="200">
            </el-table-column>

            <el-table-column
              inline-template
              label=""
              width="100">
              <span>
                <el-button type="text" size="small"></el-button>
              </span>
            </el-table-column>

            <el-table-column
              inline-template
              label=""
              width="100">
              <span>
                <el-button size="small"></el-button>
              </span>
            </el-table-column>
            
            <el-table-column
              inline-template
              fixed="right"
              label=""
              width="200">
              <span>
                <el-button-group>
                  <el-button size="small"></el-button>
                  <el-button size="small"></el-button>
                  <el-button size="small" @click.native="onDelete(row)"></el-button>
                </el-button-group>
              </span>
            </el-table-column>

           </el-table>
          <div class="pagination-wrapper">
            <el-pagination
             layout="total, prev, pager, next"
             @current-change="handleCurrentChange"
             :current-page="pager.currentPage"
             :page-size="pager.pageSize"
             :total="pager.total">
            </el-pagination>
          </div>
        </PanelTable>
      </Panel>
    </LayoutMain>
  </div>
</template> 
<script>

import Api from 'src/api/index.js'
import { CODE_SUCCESS } from 'src/const.js'

const DEFAULT_FORM = {
  name: '',
  name2: '',
  region: '',
  cascader: [''],
  type: '',
  date1: '',
  date2: '',
  time: '',
}

let options = [
  {
    label: '',
    value: '',
    children: [
      {
        label: '',
        value: '',
        children: [
          {
            label: '',
            value: '',
          },
          {
            label: '',
            value: '',
          }
        ]
      }
    ]
  },
  {
    label: '',
    value: '',
    children: [
      {
        label: '',
        value: '',
        children: [
          {
            label: '',
            value: '',
          }
        ]
      }
    ]
  }
]

export default {
  data () {
    return {
      loading: false,
      formDetailToggle: false,
      searchForm: Object.assign({}, DEFAULT_FORM),
      options: options,
      tableData: [
	      // {
	      //   createdDate: '2016-05-03',
	      //   name: '',
	      //   email: ' 1518 '
	      // }
      ],
      pager: {
        total: 0,
        currentPage: 0,
        pageSize: 10
      },
    }
  },
  methods: {
    onSearch() {
      console.log('submit!');
      this.searchForm.cascader = ['']
      this.fetchData()
    },
    onClear() {
      // this.$refs.searchForm.resetFields();
      this.searchForm = Object.assign({}, DEFAULT_FORM)
    },
    onToggle() {
      this.formDetailToggle = !this.formDetailToggle
    },
  	handleCurrentChange(currentPage) {
  		this.fetchData(currentPage)
  	},
    onDelete(item) {
      this.$confirm(`[ID=${item.id}], ?`, '', {
        type: 'warning'
      })
      .then(() => {
        this.$message({
          type: 'success',
          message: '!'
        })
      })
      .catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    onChange(value, selectedOptions) {
      var vm = this
      console.log(value, selectedOptions)
      console.log(vm.searchForm.cascader)
    },
  	fetchData(currentPage) {
      let vm = this

      currentPage = currentPage || this.pager.currentPage
  		this.loading = true
      Api.app.getAllAreas({
        pageNo: currentPage
      })
      .then(res => {  
        if (res.data.code === CODE_SUCCESS) {
  		    // 
          let resData = res.data.data

          vm.tableData = resData.list.map(el => {
            return {
              id: el.id,
              profile: el.profile,
              createdDate: el.createdDate,
              name: el.name,
              email: el.email,
            }
          })
          vm.pager.currentPage = resData.pager.current
          vm.pager.total = resData.pager.total
        } else {
          // console.log('----------------')
        }
  		})
      .then(() => {  
        this.loading = false
      })
      .catch(error => {
        // 
      }) 
  	}
  },
  mounted: function() {
    // this.fetchData()
  }

}
</script>

<style scpoed lang="less">

</style>



